import React from 'react';
import PropTypes from 'prop-types';


import classNames from 'classnames';

import './button.css';
import './effects.css';

export const theme = {
	CLASSES: 'rmd-btn',

	CLASSES_FLAT: 'rmd-btn__flat',
	CLASSES_RAISED: 'rmd-btn__raised',
	CLASSES_FAB: 'rmd-btn__fab',

	CLASSES_DEFAULT: 'rmd-btn__default',
	CLASSES_PRIMARY: 'rmd-btn__primary',
	CLASSES_SECONDARY: 'rmd-btn__secondary',
};

const Button = ({ _theme = theme, ...props }) => {

	const { btnStyle, type, ...otherProps } = props;

	return <button
		{ ...otherProps }
		className={ classNames(_theme.CLASSES, props.class, props.className, {
			[_theme.CLASSES_FLAT]: btnStyle === 'flat' || btnStyle === undefined,
			[_theme.CLASSES_RAISED]: btnStyle === 'raised',
			[_theme.CLASSES_FAB]: btnStyle === 'fab',
			[_theme.CLASSES_DEFAULT]: type === 'default' || type === undefined,
			[_theme.CLASSES_PRIMARY]: type === 'primary',
			[_theme.CLASSES_SECONDARY]: type === 'secondary',
		}) }>
		{ otherProps.children }
	</button>;
};

Button.propTypes = {
	btnStyle: PropTypes.oneOf(['flat', 'raised', 'fab']),
 	type: PropTypes.oneOf(['default', 'primary', 'secondary']),
};


export default Button;



