import React, { Component } from 'react';

import ButtonSection from './Buttons';
import TooltipSection from './Tooltips';


import './App.css';

class App extends Component {
    render() {
        return (
            <div className="cv-demo">
                <section className="cv-card">
                    <h1 style={{marginBottom: 0}}>React Components Demo Page</h1>
                </section>
                <ButtonSection/>
                <TooltipSection/>
            </div>
        );
    }
}

export default App;



